.Campaigns{
    padding: 43px 0 60px 0;
}
.Proactive{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.Campaigns span {
    background-color: #D5EAFB;
    padding: 3px 10px;
    border-radius: 11px;
    display: inline-block;
    text-transform: uppercase;
    color: #666;
    font-family: Sailec-Medium, Helvetica, sans-serif;
    font-weight: 600;
    font-size: 12px;
    line-height: 21px;
    letter-spacing: 1px;
}
.Campaigns h2{
    font-weight: 500;
    font-size: 36px;
    line-height: 48px;
    color: #4d4d4d;
    padding-right: 47px;
}
.subtitle {
    color: #666666;
    font-family: National-Book, Helvetica, sans-serif;
    font-size: 20px;
    line-height: 28px;
    padding-right: 115px;
}
a{
    color: #3995dc;
    font-family: National-Book, Helvetica, sans-serif;
    font-size: 17px;
    line-height: 28px;
    font-weight: 600;
}